@import url(https://fonts.googleapis.com/css2?family=Prompt&display=swap);
@font-face{font-family:"F2";src:url(/kart-optimizer/static/media/mario_kart_f2.0651abbb.ttf) format("truetype")}.Header{grid-area:header;width:100%;height:100%;padding:1rem;box-sizing:border-box;background:linear-gradient(#26baff, #f92470);box-shadow:0 5px 10px rgba(0,0,0,0.25);line-height:4rem;word-spacing:-10px}.Logo{height:100%;width:45rem;margin:auto}.LogoImg{float:left;height:100%;margin-right:1rem}.LogoText{color:white;font-size:28px;font-family:"F2";-webkit-text-stroke-width:2px;-webkit-text-stroke-color:#333333}

.KartConfig{grid-area:kart-config;display:flex;justify-content:center;align-items:center;box-sizing:border-box;width:100%;height:100%}.CenterContainer{width:40rem;height:42.25rem}.PartOptions{display:flex}.Separator{width:calc(100% - 1rem);height:0.25rem;background:linear-gradient(to right, #26baff, #f92470);border-radius:1rem;margin:1rem 0.5rem}

.PartTile{position:relative;display:flex;justify-content:center;box-sizing:border-box;align-items:center;box-sizing:border-box;width:4rem;height:4rem;margin:0.5rem;padding:0.25rem;border-radius:1rem;background:#555}.PartTile:hover{-webkit-filter:brightness(80%);filter:brightness(80%)}.PartImage{width:100%}

.PartList{display:flex;flex-wrap:wrap;justify-content:center}

.PartSelection{display:flex;align-items:center;justify-content:center;box-sizing:border-box;width:9rem;height:9rem;padding:0.5rem;margin:0.5rem;border-radius:2rem;background:#777;transition:0.25s}.PartSelection:hover{-webkit-filter:brightness(80%);filter:brightness(80%)}

*{border:0;margin:0;padding:0}body{font-family:"Prompt", Arial, sans-serif;width:100vw;height:100vh;background:#333333;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.Grid{display:grid;grid-template-areas:"header header" "kart-config kart-config" "kart-config kart-config";grid-template-rows:6rem calc(50vh - 3rem) calc(50vh - 3rem);grid-template-columns:50vw 50vw}

